const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.last')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)

window.hashMap = xObject || [
    { logo: 'X', url: 'https://xiedaimala.com' },
    { logo: 'G', url: 'https://github.com/' },
]
const simplifyUrl = (url) => {
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '') //删除 / 开头的内容
}

const render = () => {
    $siteList.find('li:not(.last)').remove()
    hashMap.forEach((node, index) => {
        const $li = $(`<li>
            <div class="site">
                <div class="log">${node.logo[0]}</div>
                <div class="link">${simplifyUrl(node.url)}</div >
                <div class="close">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-close"></use>
                    </svg></div>
            </div >
        </li > `).insertBefore($lastLi)
        $li.on('click', () => {
            window.open(node.url, target = '_self')
        })
        $li.on('click', '.close', (e) => {
            console.log('这里')
            e.stopPropagation()
            //e.preventDefault() 不能阻止冒泡，但能阻止默认事件
            //return false 既阻止冒泡也阻止默认事件
            hashMap.splice(index, 1)
            render()
        })
    })
}
render()

$('.addButton')
    .on('click', () => {
        let url = window.prompt('请问你要添加的网址是啥？', '请输入')
        if (url.indexOf('http') !== 0) {
            url = 'https://' + url
        }
        console.log(url)
        hashMap.push({
            logo: simplifyUrl(url)[0].toUpperCase(),
            logoType: 'text',
            url: url,
        })
        render()
    })

window.onbeforeunload = () => {
    console.log('页面要关闭了')
    const string = JSON.stringify(hashMap)
    localStorage.setItem('x', string)
}

$(document).on('keypress', (e) => {
    const { key } = e
    for (let i = 0; i < hashMap.length; i++) {
        if (hashMap[i].logo.toLowerCase() === key) {
            window.open(hashMap[i].url, target = '_self')
        }
    }
})